import {
  Directive,
  forwardRef,
  Attribute,
  OnChanges,
  SimpleChanges,
  Input,
} from "@angular/core";
import {
  NG_VALIDATORS,
  Validator,
  AbstractControl,
  ValidationErrors,
  Validators,
} from "@angular/forms";

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: "[exactMatch]",
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => ExactMatchDirective),
      multi: true,
    },
  ],
})
export class ExactMatchDirective implements Validator {
  private valFn = Validators.nullValidator;

  constructor(@Attribute("exactMatch") public exactMatch: string) {}

  validate(control: AbstractControl): ValidationErrors {
    const duplicate = control.value;

    const real = control.root.get(this.exactMatch);
    if (real && duplicate !== real.value) {
      return {
        exactMatch: false,
      };
    }
    return null;
  }
}
